import React from 'react';
import { Montserrat } from 'next/font/google';
import { motion } from 'framer-motion';

const montserrat = Montserrat({ subsets: ['latin'] });

const HowItWorks: React.FC = () => {
  const steps = [
    { title: 'Create Your Profile', description: 'Sign up and showcase your skills.' },
    { title: 'Get Matched', description: 'AI matches candidates to the right jobs.' },
    { title: 'Skill Assessment', description: 'Automated coding & aptitude tests.' },
    { title: 'Hire with Confidence', description: 'Data-backed hiring decisions.' },
  ];

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.3,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.6, ease: 'easeOut' } },
  };

  return (
    <section className={`${montserrat.className} bg-gradient-to-br from-gray-900 to-black text-white py-16 sm:py-24 lg:py-32`}>
      <div className="container mx-auto px-4 text-center">
        <motion.h2
          className="font-headline text-4xl font-bold tracking-tight sm:text-5xl"
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: 'easeOut' }}
        >
          How HireSense AI Works
        </motion.h2>
        <motion.p
          className="mt-4 text-xl text-gray-400"
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: 'easeOut', delay: 0.2 }}
        >
          Your hiring, simplified and powered by AI.
        </motion.p>

        <motion.div
          className="mt-16 grid grid-cols-1 gap-8 md:grid-cols-2 lg:grid-cols-4"
          variants={containerVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.3 }}
        >
          {steps.map((step, index) => (
            <motion.div
              key={index}
              className="rounded-lg bg-gray-800/50 p-6 shadow-lg border border-gray-700"
              variants={itemVariants}
            >
              <div className="flex items-center justify-center size-12 rounded-full bg-primary/20 text-primary-foreground font-bold text-xl mx-auto mb-4">
                {index + 1}
              </div>
              <h3 className="text-xl font-semibold mb-2">{step.title}</h3>
              <p className="text-gray-300">{step.description}</p>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </section>
  );
};

export default HowItWorks;